import { Paginations } from './class-pagination';

export class LocalSave extends Paginations {
  constructor() {
    super();
  }
  // ***************邪械 胁 谢芯谐懈泻 *******************************************
  lokalStart = () => {
    this.start();
    this.getLocalThema(); //锌芯胁械懈屑 芯邪薪械薪薪 械屑
    this.getLocalLanguage(); //锌芯胁械械屑 薪邪 泻邪泻芯屑 蟹泻械 斜谢邪 胁泻谢邪写泻邪
    this.getLocalInputText(); //锌芯胁械懈屑 懈泻邪谢 谢懈 芯薪 泻懈薪芯 懈谢懈 薪械
    this.getLibraryTrue(); //锌芯胁械械屑 斜谢 谢懈 锌芯谢蟹芯胁邪械谢 胁 斜懈斜谢懈芯械泻械 懈谢懈 薪邪 邪芯胁芯泄 邪薪懈械
    this.getArreyWatched(); //锌芯胁械械懈屑 泻邪泻懈械 懈谢屑 芯邪薪械薪 胁 锌芯屑芯械薪
    this.getArreyQueue(); //锌芯胁械懈屑 泻邪泻懈械 懈谢屑 芯邪薪械薪 胁 芯谢芯卸械薪
    this.getLocalCurrentPage(); //锌芯胁械械屑 邪薪懈 薪邪 泻芯芯芯泄 薪邪芯写懈谢 锌芯谢蟹芯胁邪械谢
    if (this.libraryTrue) {
      //薪邪泻芯薪械 邪械屑
      this.paginationLibrarySave(this.libraryTrueBt); //锌芯胁械械屑 胁 泻邪泻芯泄 泻薪芯锌泻械 屑
    } else {
      this.paginationStart(this.searchQuery);
    }
  };
  // *********************蟹邪锌懈 写邪薪薪 胁 谢芯泻邪谢泻********************************
  // 芯写邪屑芯 写邪薪 胁 谢芯泻邪谢泻 蟹 薪锌
  setLocalInput = () => {
    localStorage.setItem('search-input-text', this.searchQuery);
  };
  // 芯写邪屑芯 写邪薪 胁 谢芯泻邪谢泻 蟹 屑芯胁懈
  setLocalLanguage = () => {
    localStorage.setItem('language', this.curentLanguage);
  };
  //  芯写邪屑芯 写邪薪 锌芯 械屑 胁 谢芯泻邪谢泻
  setLocalThema = () => {
    localStorage.setItem('thema', JSON.stringify(this.refs.themaBt.hasAttribute('checked')));
  };
  // 邪锌懈 胁 谢芯泻邪谢泻 写邪薪 锌芯 谢屑 锌械谐谢薪
  setFilmWached = () => {
    localStorage.setItem('wached-film', JSON.stringify(this.arrWatched));
  };
  // 邪锌懈 胁 谢芯泻邪谢泻 写邪薪 锌芯 谢屑 写芯写邪薪 胁 械谐
  setFilmQueue = () => {
    localStorage.setItem('queue-film', JSON.stringify(this.arrQueue));
  };
  // 屑械芯写 蟹邪锌懈胁邪械 谐写械 薪邪芯写懈 锌芯谢蟹芯胁邪械谢.  谢懈斜芯 芯谢 - library
  setLibraryTrue = argument => {
    localStorage.setItem('is-library', JSON.stringify(argument));
    this.libraryTrue = argument;
  };
  setHeaderWatchedBtnTrue = argument => {
    this.libraryTrueBt = argument;
    localStorage.setItem('is-watched-btn', JSON.stringify(argument));
  };

  // *******************械薪懈械 谢芯泻邪谢泻懈*********************************************
  getHeaderBtnTrue = () => {
    const WatchedBtnTrue = localStorage.getItem('is-watched-btn'); //锌芯胁械懈屑 薪邪 泻邪泻芯泄 泻薪芯锌泻械 斜谢 锌芯谢蟹芯胁邪械谢
    if (WatchedBtnTrue) {
      this.libraryTrueBt = JSON.parse(WatchedBtnTrue); //  懈谢懈 芯谢
      if (this.libraryTrueBt) {
        console.log('锌邪谐薪邪 锌械械谐谢薪');
        this.onWatchedClick();
        // 孝校孝  挟 泻 薪泻 写芯写邪懈 芯斜 械薪写芯懈谢邪 邪 邪屑邪 芯薪泻邪 芯  锌懈 泻谢泻 薪邪 泻薪芯锌泻
        // this.paginationLibrarySave(true);
      } else {
        console.log('锌邪谐薪邪 写芯写邪懈 写芯 锌械械谐谢写');
        // this.currentPage = 1;
        this.onQueueClick();
        // 孝校孝  挟 泻 薪泻 写芯写邪懈 芯斜 械薪写芯懈谢邪 邪 邪屑邪 芯薪泻邪 芯  锌懈 泻谢泻 薪邪 泻薪芯锌泻
        // this.paginationLibrarySave(false);
      }
    }
  };

  getArreyWatched = () => {
    const arreyWatched = localStorage.getItem('wached-film');
    if (arreyWatched) {
      this.arrWatched = JSON.parse(arreyWatched);
    }
  };
  getArreyQueue = () => {
    const arreyQueue = localStorage.getItem('queue-film');
    if (arreyQueue) {
      this.arrQueue = JSON.parse(arreyQueue);
    }
  };

  getLibraryTrue = () => {
    const libraryIsTrue = localStorage.getItem('is-library'); // 锌芯胁械懈屑 斜谢懈 谢懈 屑 胁 斜懈斜谢懈芯械泻械
    if (libraryIsTrue) {
      this.libraryTrue = JSON.parse(libraryIsTrue);
      if (this.libraryTrue) {
        //  懈谢懈 邪谢
        this.getHeaderBtnTrue(); //蟹邪锌懈屑 锌芯胁械泻 胁斜邪薪 斜懈斜谢懈芯械泻 芯谢泻芯 械谢懈 屑 斜谢懈 胁 斜懈斜谢懈芯械泻械
        this.onLibraryClick(); // 芯懈械屑 邪薪懈 斜懈斜谢懈芯械泻懈
      }
    }
  };
  // 肖薪泻 锌芯谢邪屑芯 写邪薪 蟹 谢芯泻邪谢泻懈 写谢 薪锌
  getLocalInputText = () => {
    const inputText = localStorage.getItem('search-input-text');
    if (inputText) {
      this.refs.inputFilm.value = inputText;
      this.searchQuery = inputText;
      this.paginationStart(inputText);
    }
  };
  // 芯谢邪屑芯 写邪薪 蟹 谢芯泻邪谢泻懈 写谢 屑芯胁懈
  getLocalLanguage = () => {
    const selectLanguage = localStorage.getItem('language');
    if (selectLanguage) {
      this.curentLanguage = selectLanguage;
      if (this.curentLanguage === 'en') {
        this.onEnClick();
      } else {
        this.onUaClick();
      }
    } else {
      this.onEnClick();
    }
  };

  // 芯谢邪屑芯 写邪薪 蟹 谢芯泻邪谢泻懈 写谢 械屑懈
  getLocalThema = () => {
    const selectThema = localStorage.getItem('thema');
    if (selectThema) {
      if (selectThema === 'true') this.onThemaClick();
    }
  };

  // 写邪薪 写谢 芯薪泻懈
  getLocalCurrentPage = () => {
    const currentPage = localStorage.getItem('currentPage');
    if (currentPage) {
      this.currentPage = JSON.parse(currentPage);
    }
  };
}
