import debounce from 'debounce';
import { LocalSave } from './class-localsave';

export class Listener extends LocalSave {
  constructor() {
    super();
  }
  listenerStart = () => {
    this.EventListenerAll(); //邪械屑 芯斜蟹邪械谢薪械 谢邪械谢懈
    this.lokalStart();
  };

  EventListenerAll = () => {
    this.scrollWindow();
    // 械薪械 谢芯谐芯
    this.refs.logo.addEventListener('click', () => {
      this.onWatchedClick(); //芯 蟹邪谐谢泻邪 锌械械泻懈写胁邪邪 锌芯谢蟹芯胁邪械谢 胁械谐写邪 胁 锌芯屑芯械薪薪械 邪泻 泻邪泻 邪芯胁邪 薪泻懈 械薪写械懈 锌芯屑芯械薪薪械
      this.setLibraryTrue(false);
      this.goHomePage();
      this.onHomeClick();
    });
    // 械械薪 芯屑
    this.refs.homeBt.addEventListener('click', () => {
      this.onWatchedClick(); //芯 蟹邪谐谢泻邪 锌械械泻懈写胁邪邪 锌芯谢蟹芯胁邪械谢 胁械谐写邪 胁 锌芯屑芯械薪薪械 邪泻 泻邪泻 邪芯胁邪 薪泻懈 械薪写械懈 锌芯屑芯械薪薪械
      this.setLibraryTrue(false);
      this.goHomePage();
      this.onHomeClick();
    });
    // 械薪械 薪邪 薪锌
    this.refs.inputFilm.addEventListener('input', debounce(this.onInputSearch, 1000));

    // 械薪械 薪邪 泻谢泻 锌芯 屑芯胁懈
    this.refs.enBox.addEventListener('click', () => {
      this.onEnClick();
      if (this.libraryTrue) {
        this.paginationLibrarySave(this.libraryTrueBt); // 锌械械械薪写械薪谐 斜斜谢芯械泻懈 胁 蟹邪谢械卸薪芯 胁写 芯斜邪薪芯 屑芯胁懈
      }
      this.setLocalLanguage();
      const libraryBtClasses = this.refs.libraryBt.className.split(' ');
      if (libraryBtClasses.includes('button-nav--current')) {
        return;
      }
      this.paginationStart(this.searchQuery); // 械谢懈 锌芯谢蟹芯胁邪械谢 屑械薪懈谢 蟹泻 懈  薪械谐芯 芯邪薪械薪芯 锌芯懈泻芯胁芯械 谢芯胁芯 蟹薪邪懈 锌械械写邪械屑  邪 懈薪邪械 锌芯锌谢薪械 懈谢屑 薪邪泄写械
    });
    // 械薪械 薪邪 泻谢泻 锌芯 屑芯胁懈
    this.refs.uaBox.addEventListener('click', () => {
      this.onUaClick();
      if (this.libraryTrue) {
        this.paginationLibrarySave(this.libraryTrueBt); // 锌械械械薪写械薪谐 斜斜谢芯械泻懈 胁 蟹邪谢械卸薪芯 胁写 芯斜邪薪芯 屑芯胁懈
      }
      this.setLocalLanguage();
      const libraryBtClasses = this.refs.libraryBt.className.split(' ');
      if (libraryBtClasses.includes('button-nav--current')) {
        return;
      }
      this.paginationStart(this.searchQuery); // 械谢懈 锌芯谢蟹芯胁邪械谢 屑械薪懈谢 蟹泻 懈  薪械谐芯 芯邪薪械薪芯 锌芯懈泻芯胁芯械 谢芯胁芯 蟹薪邪懈 锌械械写邪械屑  邪 懈薪邪械 锌芯锌谢薪械 懈谢屑 薪邪泄写械
    });

    // 械薪械 薪邪 泻谢泻懈 锌芯 胁懈斜芯 械屑懈
    this.refs.themaBt.addEventListener('click', () => {
      this.onThemaClick();
      this.setLocalThema();
    });
    // 械薪械 薪邪 泻谢泻懈 锌芯 泻薪芯锌 斜斜谢芯械泻邪
    this.refs.libraryBt.addEventListener('click', () => {
      this.currentPage = 1;
      this.onWatchedClick(); // 芯斜 胁芯蟹胁邪邪谢芯 薪邪 邪芯胁 胁泻谢邪写泻 Watched
      this.setLibraryTrue(true);
      // 蟹邪蟹 蟹邪锌懈  写谢 泻薪芯锌泻懈 锌械械谐谢薪
      this.setHeaderWatchedBtnTrue(true);
      this.onLibraryClick();
      this.paginationLibrarySave(true); //true 写谢 锌芯屑芯械薪 懈谢屑芯胁
    });

    // 械薪械 锌芯 泻谢泻 薪邪 屑芯写邪谢泻 泻薪芯锌泻邪 锌械械谐谢薪
    this.refs.modalWatchedBt.addEventListener('click', () => {
      if (this.arrWatched.includes(this.liID)) {
        this.arrWatched.splice(this.arrWatched.indexOf(this.liID), 1);
        if (this.libraryTrue) {
          if (this.libraryTrueBt) {
            this.paginationLibrarySave(true);
          }
        }
      } else {
        this.arrWatched.push(this.liID);
        if (this.libraryTrue) {
          if (this.libraryTrueBt) {
            this.paginationLibrarySave(true);
          }
        }
      }
      this.setFilmWached(true);
      this.isFilmsSave();
    });

    // 械薪械 锌芯 泻谢泻 薪邪 屑芯写邪谢泻 泻薪芯锌泻邪 械谐邪
    this.refs.modalQueueBt.addEventListener('click', () => {
      if (this.arrQueue.includes(this.liID)) {
        this.arrQueue.splice(this.arrQueue.indexOf(this.liID), 1);
        // 械 械斜邪 写芯写邪胁邪懈 谢泻懈 泻芯 屑懈 胁写泻懈谢懈 斜懈斜谢懈芯械泻
        if (this.libraryTrue) {
          if (!this.libraryTrueBt) {
            this.paginationLibrarySave(false);
          }
        }
      } else {
        this.arrQueue.push(this.liID);
        if (this.libraryTrue) {
          if (!this.libraryTrueBt) {
            this.paginationLibrarySave(false);
          }
        }
      }
      this.setFilmQueue(false);
      this.isFilmsSave();
    });

    this.openModalFooter();
    // 谢邪械谢 写谢 泻薪芯锌芯泻 斜懈斜谢懈芯械泻懈 胁 械写械械
    this.refs.headerWathedBtn.addEventListener('click', () => {
      this.currentPage = 1;
      this.onWatchedClick();
      this.setHeaderWatchedBtnTrue(true);
      this.paginationLibrarySave(this.libraryTrueBt); //true 写谢 锌芯屑芯械薪 懈谢屑芯胁
    });
    this.refs.headerQueueBtn.addEventListener('click', () => {
      this.currentPage = 1;
      this.onQueueClick();
      this.setHeaderWatchedBtnTrue(false);
      this.paginationLibrarySave(this.libraryTrueBt); //false 写谢  锌芯屑芯械薪 懈谢屑芯胁
    });
  };
  //锌芯懈泻 懈谢屑邪 锌芯 胁胁械写械薪芯屑 谢芯胁
  onInputSearch = evt => {
    this.currentPage = 1;
    this.setCurrentPage();
    if (!evt.target.value.trim()) {
      localStorage.removeItem('search-input-text');
      this.searchQuery = evt.target.value.trim().toLowerCase(); // 蟹邪锌懈胁邪械屑 蟹薪邪械薪懈械 胁 锌械械屑械薪薪
      this.paginationStart(evt.target.value.trim().toLowerCase()); //芯 芯谐芯 械 谢懈 锌芯懈泻芯胁芯械 谢芯胁芯 斜写械  谢懈斜芯 芯谢 懈 屑械芯写 锌芯泄屑械 泻邪泻芯泄 蟹邪锌芯 薪卸械薪
      return;
    }
    this.searchQuery = evt.target.value.trim().toLowerCase();
    this.searchQuery = evt.target.value.trim().toLowerCase(); // 蟹邪锌懈胁邪械屑 蟹薪邪械薪懈械 胁 锌械械屑械薪薪
    this.setLocalInput();
    this.paginationStart(evt.target.value.trim().toLowerCase()); //芯 芯谐芯 械 谢懈 锌芯懈泻芯胁芯械 谢芯胁芯 斜写械  谢懈斜芯 芯谢 懈 屑械芯写 锌芯泄屑械 泻邪泻芯泄 蟹邪锌芯 薪卸械薪
  };
  //  胁芯蟹胁邪邪械 泻 锌芯锌谢薪屑 懈谢屑邪屑 邪芯胁芯泄 邪薪懈
  goHomePage = evt => {
    this.currentPage = 1;
    localStorage.removeItem('search-input-text');
    localStorage.removeItem('currentPage');
    this.refs.inputFilm.value = '';
    this.currentPage = 1;
    this.paginationStart(false);
  };

  //=========泻芯谢谢 芯泻薪邪==========
  scrollWindow = () => {
    // window.addEventListener('DOMContentLoaded', () => {
    //
    //   this.refs.upScroll.classList.add('visually-hidden');
    // });
    this.refs.upScroll.addEventListener('click', () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    });

    window.addEventListener(
      'scroll',
      debounce(() => {
        scrollY > 200
          ? this.refs.upScroll.classList.remove('visually-hidden')
          : this.refs.upScroll.classList.add('visually-hidden');
      }, 250),
    );
  };
}
